<template>
	<view class="container">
		<view class="user-section">
			<image class="bg" src="/static/user-bg.png"></image>
			<view class="user-info-box">
				<view class="portrait-box">
					<image class="portrait" :src="userInfo.photoUrl || '/static/missing-face.png'"></image>
				</view>
				<view class="customer-service" @click="customerService">
					<text class="iconfont yticon icon-kefu1"></text>
					<view class="customer-service-text">客服</view>
				</view>
			</view>
			<view class="vip-card-box">
				<!-- <view class="b-btn" v-if="!hasLogin" @click="navTo('/pages/public/login')"> -->
				<view class="b-btn" v-if="!hasLogin" @click="navTo('/pages/public/login')">
					立即登录
				</view>
				<!-- <view class="b-btn" v-if="hasLogin" @click="navTo('/pages/set/setUserInfoName')">
					{{userInfo.name||'设置昵称'}}
				</view> -->
				<view class="tit">
					<text class="yticon icon-iLinkapp-"></text>
					<text v-if="hasLogin">{{userInfo.username}} </text>
					<text v-if="!hasLogin">游客</text>
				</view>
				<text class="e-m"></text>
				<text class="e-b"></text>
			</view>
		</view>

		<view class="cover-container" :style="[{
				transform: coverTransform,
				transition: coverTransition
			}]" @touchstart="coverTouchstart" @touchmove="coverTouchmove" @touchend="coverTouchend">
			<image class="arc" src="/static/arc.png"></image>

			<!-- 订单 -->
			<view class="order-section">
				<view class="order-item" @click="navTo('/pages/order/order?state=')" hover-class="common-hover"
					:hover-stay-time="50">
					<text class="yticon icon-shouye"></text>
					<text>全部订单</text>
				</view>
				<view class="order-item" @click="navTo('/pages/order/order?state=1')" hover-class="common-hover"
					:hover-stay-time="50">
					<text class="yticon icon-daifukuan"></text>
					<text>待付款</text><uni-badge type="error" v-if="toPayOrderCount>0"
						:text="toPayOrderCount+''"></uni-badge>
				</view>
				<view class="order-item" @click="navTo('/pages/order/order?state=3')" hover-class="common-hover"
					:hover-stay-time="50">
					<text class="yticon icon-yishouhuo"></text>
					<text>待收货</text><uni-badge type="error" v-if="toConfirmOrderCount>0"
						:text="toConfirmOrderCount+''"></uni-badge>
				</view>
				<view class="order-item" @click="navTo('/pages/aftersale/list')" hover-class="common-hover"
					:hover-stay-time="50">
					<text class="yticon icon-shouhoutuikuan"></text>
					<text>退款/售后</text><uni-badge type="error" v-if="afterSaleCount>0"
						:text="afterSaleCount+''"></uni-badge>
				</view>
			</view>
			<!-- 浏览历史 -->
			<view class="history-section icon">
				<view class="sec-header" v-if="footPrint.length>0">
					<text class="yticon icon-lishijilu"></text>
					<text>浏览历史</text>
				</view>
				<scroll-view scroll-x class="h-list" v-if="footPrint.length>0">
					<image @click="navTo('/pages/product/product?id='+footPrint[footPrint.length-index-1].productUuid)"
						:src="footPrint[footPrint.length-index-1].productMainImage.url" mode="aspectFill"
						v-for="(product,index) in footPrint"></image>
				</scroll-view>
				<list-cell icon="icon-shouhuodizhi" iconColor="#5fcda2" title="地址管理"
					@eventClick="navTo('/pages/address/address')"></list-cell>
				<list-cell v-if="hasLogin" @eventClick="togglePopup('bottom', 'share')" icon="icon-share"
					iconColor="#9789f7" title="分享" tips="快邀请好友来体验吧!"></list-cell>
				<list-cell icon="icon-shoucang_xuanzhongzhuangtai" iconColor="#54b4ef" title="我的收藏"
					@eventClick="navTo('/pages/product/favorite')"></list-cell>
				<list-cell icon="icon-bangzhu" iconColor="#ee883b" title="帮助"
					@eventClick="navTo('/pages/help/help')"></list-cell>
				<list-cell icon="icon-shezhi" iconColor="#e07472" title="设置" border=""
					@eventClick="navTo('/pages/set/set')"></list-cell>
			</view>
			<view class="empty"></view>
		</view>
		<!-- 底部分享弹窗 -->
		<uni-popup ref="showshare" :type="type">
			<shareByWx :shareType='1' :userId="userInfo.id" :shareImg="userInfo.photoUrl" shareTitle="快来药食同源大健康看看吧!"
				:shareContent="'我是'+userInfo.nickname" :shareHref="'/pages/public/login?id='+userInfo.userUuid"
				:inviteCode="userInfo.inviteCode" @close="closeShare()"></shareByWx>
		</uni-popup>

	</view>
</template>
<script>
	import listCell from '@/components/mix-list-cell';
	import uniBadge from "@/components/uni-badge/uni-badge.vue";
	import uniPopup from "@/components/uni-popup/uni-popup.vue"
	import shareByWx from '@/components/share/shareByWx.vue'

	import {
		mapState,
		mapMutations
	} from 'vuex';
	let startY = 0,
		moveY = 0,
		pageAtTop = true;
	export default {
		components: {
			listCell,
			uniBadge,
			uniPopup,
			shareByWx
		},
		data() {
			return {
				type: '',
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				toPayOrderCount: 0,
				toConfirmOrderCount: 0,
				afterSaleCount: 0,
				userInfo: {}
			}
		},
		onLoad() {},
		onShow() {
			//同步用户信息
			let that = this;
			if (this.hasLogin) {
				this.$api.request.userInfo({}, res => {
					this.userInfo = res.data
				});
				//获取待付款和待收货订单数量(0-待付款, 1-待发货， 2-待收货)
				// this.inquiryOrderTotal('0');
				// this.inquiryOrderTotal('2');
				// this.inquiryAfterSaleTotal(['0', '1', '3']);
			}
		},
		onShareAppMessage(res) {
			return {
				title: '我是' + this.userInfo.name + ',快来药食同源大健康平台看看吧！',
				imageUrl: this.userInfo.photoUrl,
				path: '/pages/public/login?inviteCode=' + this.userInfo.inviteCode
			}
		},
		// #ifndef MP
		onNavigationBarButtonTap(e) {
			const index = e.index;
			if (index === 0) {
				this.navTo('/pages/set/set');
			} else if (index === 1) {
				uni.navigateTo({
					url: '/pages/notice/list'
				})
			}
		},
		// #endif
		computed: {
			...mapState(['hasLogin', 'userInfo', 'footPrint', 'applicationConfig']),
			shareHref() {
				let pages = getCurrentPages()
				let page = pages[pages.length - 1]
				// #ifdef H5 || MP-WEIXIN || APP-PLUS || APP-PLUS-NVUE
				return this.$api.request.apiBaseUrl.replace("/b2b2c/rest/", "") + '/#/pages/public/register?id=' + this
					.userInfo.userUuid;
				// #endif

			}
		},
		methods: {
			...mapMutations(['login']),
			login() {
				var that = this;
				wx.login({
					success(res) {
						if (res.code) {
							//发起网络请求
							that.$api.request.login({
								code: res.code
							}, res => {
								if (res.body.code === '0') {
									var data = res.body.data;
								} else {
									console.log(res.body.message)
								}
							})
						} else {
							console.log('登录失败！' + res.errMsg)
						}
					}
				})
			},
			toggleSignPoint: function() {
				uni.navigateTo({
					url: '/pages/point/sign'
				})
			},
			togglePopup(type, open) {
				switch (type) {
					case 'top':
						this.content = '顶部弹出 popup'
						break
					case 'bottom':
						this.content = '底部弹出 popup'
						break
					case 'center':
						this.content = '居中弹出 popup'
						break
				}
				this.type = type
				this.$nextTick(() => {
					this.$refs['show' + open].open()
				})
			},
			closeShare() {
				this.$refs.showshare.close();
			},
			// 查询订单总数
			inquiryOrderTotal(orderStatus) {
				let searchOptions = {
					userUuid: this.userInfo.userUuid,
					status: orderStatus,
					isPagination: false
				};
				this.$api.request.searchOrderTotal(searchOptions, res => {
					if (res.data.code === 200) {
						var total = res.data.data.total;
						if (orderStatus == '0') {
							this.toPayOrderCount = total;
						} else if (orderStatus == '2') {
							this.toConfirmOrderCount = total;
						}
					} else {
						console.log(res.data.message)
					}
				}, true);
			},
			// 查询售后订单数量
			inquiryAfterSaleTotal(statusList) {
				let searchOptions = {
					userUuid: this.userInfo.userUuid
				};
				if (statusList && statusList.length > 0) {
					keyArray.push('STATUS_LIST');
					searchOptions.statusList = statusList;
				}
				searchOptions.keyArray = keyArray;
				this.$api.request.searchAfterSaleTotal(searchOptions, res => {
					if (res.body.status.statusCode === '0') {
						var afterSaleCount = res.body.data.total;
						this.afterSaleCount = afterSaleCount;
					} else {
						console.log(res.body.status.errorDesc);
					}
				}, true);
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url) {
				if (!this.hasLogin) {
					url = '/pages/public/login';
				}
				uni.navigateTo({
					url
				})
			},
			customerService() {
				uni.navigateTo({
					url: '/pages/help/help'
				})
			},
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e) {
				if (pageAtTop === false) {
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e) {
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if (moveDistance < 0) {
					this.moving = false;
					return;
				}
				this.moving = true;
				if (moveDistance >= 80 && moveDistance < 100) {
					moveDistance = 80;
				}

				if (moveDistance > 0 && moveDistance <= 80) {
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend() {
				if (this.moving === false) {
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
		},
	}
</script>
<style lang='scss'>
	%flex-center {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	%section {
		display: flex;
		justify-content: space-around;
		align-content: center;
		background: #fff;
		border-radius: 10upx;
	}

	.sign-point {
		position: absolute;
		right: 100rpx;
		text-align: center;
		color: #fff;
	}

	.sign-point-text {
		font-size: 22rpx;
		margin-top: 4rpx;
	}

	.sign-point .iconfont {
		color: #fff;
		background: rgba(0, 0, 0, 0.1);
		width: 72rpx;
		height: 72rpx;
		text-align: center;
		line-height: 72rpx;
		border-radius: 50%;
		display: inline-block;
	}

	.customer-service {
		position: absolute;
		right: 0rpx;
		text-align: center;
		color: #fff;
	}

	.customer-service-text {
		font-size: 22rpx;
		margin-top: 4rpx;
	}

	.customer-service .iconfont {
		color: #fff;
		background: rgba(0, 0, 0, 0.1);
		width: 72rpx;
		height: 72rpx;
		text-align: center;
		line-height: 72rpx;
		border-radius: 50%;
		display: inline-block;
	}

	.user-section {
		height: 520upx;
		padding: 100upx 30upx 0;
		position: relative;

		.bg {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: -11;
			/* filter: blur(1px); */
			opacity: .8;
		}
	}

	.user-info-box {
		height: 180upx;
		display: flex;
		align-items: center;
		position: relative;
		z-index: 1;

		.portrait {
			width: 130upx;
			height: 130upx;
			border: 5upx solid #fff;
			border-radius: 50%;
		}

		.username {
			font-size: $font-sm;
			color: $font-color-base;
			margin-left: 20upx;
		}
	}

	.vip-card-box {
		display: flex;
		flex-direction: column;
		color: #f7d680;
		height: 240upx;
		background-color: #27392e;
		border-radius: 16upx 16upx 0 0;
		overflow: hidden;
		position: relative;
		padding: 20upx 24upx;

		.card-bg {
			position: absolute;
			top: 20upx;
			right: 0;
			width: 380upx;
			height: 260upx;
		}

		.b-btn {
			position: absolute;
			right: 20upx;
			top: 16upx;
			/* width: 132upx; */
			height: 40upx;
			text-align: center;
			line-height: 40upx;
			font-size: 22upx;
			color: #f6e5a3;
			border-radius: 20px;
			background: linear-gradient(left, #f9e6af, #ffd465);
			z-index: 1;
			padding: 0 20upx;
		}

		.tit {
			font-size: $font-base+2upx;
			color: #f7d680;
			margin-bottom: 28upx;

			.yticon {
				color: #f6e5a3;
				margin-right: 16upx;
			}
		}

		.e-b {
			font-size: $font-sm;
			color: #d8cba9;
			margin-top: 10upx;
		}
	}

	.cover-container {
		background: $page-color-base;
		height: 1100upx;
		margin-top: -150upx;
		padding: 0 20upx;
		position: relative;
		background: #f5f5f5;
		padding-bottom: 20upx;

		.arc {
			position: absolute;
			left: 0;
			top: -34upx;
			width: 100%;
			height: 36upx;
		}
	}

	.tj-sction {
		@extend %section;
		align-items: baseline;

		.tj-item {
			@extend %flex-center;
			flex-direction: column;
			height: 140upx;
			font-size: $font-sm;
			color: #75787d;

			.b-btn {
				margin: 10px 0;
				width: 132upx;
				height: 40upx;
				text-align: center;
				line-height: 40upx;
				font-size: 22upx;
				color: #fff;
				border-radius: 10px;
				background: linear-gradient(left, #f5cb54, #9e760d);
				z-index: 1;
			}
		}

		.num {
			font-size: $font-lg;
			color: $font-color-dark;
			margin-bottom: 8upx;
		}
	}

	.order-section {
		@extend %section;
		padding: 28upx 0;
		margin-top: 20upx;

		.order-item {
			position: relative;
			@extend %flex-center;
			width: 120upx;
			height: 120upx;

			border-radius: 10upx;
			font-size: $font-sm;
			color: $font-color-dark;
		}

		.yticon {
			font-size: 48upx;
			margin-bottom: 18upx;
			color: #639d74;
		}

		.icon-shouhoutuikuan {
			font-size: 44upx;
		}

		.uni-badge {
			position: absolute;
			top: -2px;
			left: 30px;
			background: #dd524d;
			color: #fff;
		}
	}

	.history-section {
		padding: 30upx 0 0;
		margin-top: 20upx;
		background: #fff;
		border-radius: 10upx;

		.sec-header {
			display: flex;
			align-items: center;
			font-size: $font-base;
			color: $font-color-dark;
			line-height: 40upx;
			margin-left: 30upx;

			.yticon {
				font-size: 44upx;
				color: #5eba8f;
				margin-right: 16upx;
				line-height: 40upx;
			}
		}

		.h-list {
			white-space: nowrap;
			padding: 30upx 30upx 0;

			image {
				display: inline-block;
				width: 160upx;
				height: 160upx;
				margin-right: 20upx;
				border-radius: 10upx;
			}
		}
	}

	.nearby {
		opacity: .5;
		background-color: #000;
		position: absolute;
		right: 0px;
		top: 150upx;
		width: 200upx;
		height: 60upx;
		line-height: 60upx;
		z-index: 999;
		color: #fff;
		font-size: $font-base;
		border-radius: 100upx 0px 0px 100upx;
		text-align: center;
	}
</style>